@media (min-width: 992px) {
  .ask-group {
    position: relative;
    width: 60%;
    left: 20%;
    transition: opacity 0.3s, visibility 0.3s;
    z-index: 5;
    visibility: visible;
    font-family: "Public Sans", system-ui, -apple-system, "Segoe UI", "Roboto", "Helvetica Neue", "Noto Sans", sans-serif;
    /* 3rd and beyond positions panel on right of button */
    /* first two panels positioned on left of button */
  }
  .flexbox .ask-group {
    left: 0;
    right: 0;
    margin: auto;
    max-width: 630px;
  }
  .ask-group.fade-out {
    opacity: 0;
  }
  .ask-group .panel-group {
    position: absolute;
    top: 230px;
  }
  .flexbox .ask-group .panel-group {
    display: flexbox;
    display: flex;
    width: 100%;
    justify-content: space-between;
    flex-wrap: nowrap;
  }
}
@media (min-width: 992px) and (max-width: 991px) {
  .ask-group {
    position: static;
  }
}
@media (min-width: 992px) {
  .ask-group .panel {
    display: inline;
    background: none;
    position: relative;
  }
  .flexbox .ask-group .panel {
    margin-top: 0;
    overflow: visible;
  }
  .flexbox .ask-group .panel-group .panel + .panel {
    margin-top: 0;
  }
  .ask-group .ask-panel {
    position: absolute;
    width: 100%;
    width: 380px;
  }
  .ask-group .panel:nth-child(n+4) .ask-panel {
    right: 0;
  }
  .ask-group .panel:nth-child(-n+3) .ask-panel {
    left: 0;
  }
  .ask-button.btn-primary {
    background-color: #000;
    background-color: rgba(0, 0, 0, 0.65);
    color: #fff;
    border: none;
    border-radius: 0;
  }
  .ask-button.btn-primary:hover {
    background-color: rgba(0, 0, 0, 0.8);
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    box-shadow: none;
    outline: none;
  }
  .ask-button.btn-primary:focus {
    background-color: rgba(0, 0, 0, 0.8);
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    box-shadow: none;
    outline: solid 2px #2ea3f2 !important;
  }
}
/* Optimize ask group position for tablets 992 and 1140 */
@media (min-width: 992px) and (max-width: 1200px) {
  .ask-group {
    left: 10%;
    width: 90%;
  }
  .flexbox .ask-group {
    width: 80%;
    left: 0;
  }
}
.ask-button {
  overflow: hidden;
  position: relative;
  line-height: 2.75rem;
  font-size: 1.25rem;
}
.ask-button > span {
  font-size: 1.5rem;
  padding-right: 5px;
  vertical-align: middle;
  color: rgba(255, 255, 255, 0.8);
}

.ask-options {
  padding: 0;
}
.ask-options > ul {
  padding-left: 0;
  list-style: none;
}
.ask-options li a {
  display: block;
  padding: 3px 15px;
  margin: 0;
  line-height: 2.5rem;
  color: #fff;
  background: rgba(0, 0, 0, 0.8);
}
.ask-options li a:hover {
  background: rgba(0, 0, 0, 0.9);
}
.ask-options li a:hover .more {
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
}

@media (max-width: 991px) {
  .header-single-banner {
    display: none;
  }
  .ask-group {
    position: relative;
    float: unset;
    width: 100%;
    min-height: 1px;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 24px;
    float: none;
    padding-bottom: 1px;
  }
  .ask-group .panel {
    border: none;
    background: rgba(0, 0, 0, 0.65);
  }
  .ask-group .panel-group .panel + .panel {
    margin-top: 10px;
  }
  .ask-group a.btn {
    display: block;
    width: 100%;
    background-color: transparent;
    border: none;
    color: #fff;
  }
  .ask-group a.btn:hover, .ask-group a.btn:focus, .ask-group a.btn:active {
    color: #fff;
    background-color: rgba(0, 0, 0, 0.25);
  }
  .ask-group a.btn::after {
    content: "C";
    font-family: CaGov;
    position: absolute;
    right: 10px;
    top: 50%;
    margin-top: -20px;
  }
  .ask-group a.btn.collapsed::after {
    content: "E";
  }
  .ask-group a.btn [class^=ca-gov-icon-] {
    padding-right: 10px;
    font-size: 1.75rem;
  }
}
.fixed + .ask-group {
  z-index: 5;
  position: fixed;
}

.fade.in {
  opacity: 1;
}

